const HOSTNAME = window.location.origin + '/'
let _serverhost;

switch(process.env.BACKEND){
  case 'local': _serverhost = 'http://localhost:9898'; break;
  case 'server': _serverhost = 'http://192.168.157.111:9898'; break;
  default: _serverhost = ''; break;
}
const DJANGOHOSTNAME = _serverhost

const IMGURl = DJANGOHOSTNAME
// const IMGURl = process.env.VUE_APP_VISUAL_API
const CATEGORY = [
  ['GRAPH', 'Graphs', 'GraphsPanel', '', 'icon-moxingjiegou_1'],
  ['SCALAR', 'Scalars', 'ScalarsPanel', '', 'icon-biaoliangshuju1'],
  ['MEDIA', 'Medias', 'MediasPanel', '', 'icon-meitishuju_1'],
  ['STATISTIC', 'Statistics', 'StatisticsPanel', '', 'icon-tongjifenxi_1'],
  ['EMBEDDING', 'Embeddings', 'EmbeddingsPanel', '', 'icon-jiangweifenxi_1'],
  ['FEATURE', 'Features', 'FeaturesPanel', '', 'icon-feature'],
  ['ROC', 'ROCs', 'ROCsPanel', '', 'icon-roc'],
  ['HYPERPARM', 'Hyperparms', 'HyperparmsPanel', '', 'icon-chaocanfenxi_1'],
  ['EXCEPTION', 'Exception', 'ExceptionPanel', '', 'icon-yichang3'],
  ['CUSTOM', 'Customs', 'CustomsPanel', '', 'icon-yonghudingzhi_1']
]
const CATEGORYORDER = ['graph', 'scalar', 'media', 'statistic', 'embedding', 'feature', 'roc', 'hyperparm', 'exception', 'custom']
const RUNFILESHOWFlAG = { 'graph': 0, 'scalar': 1, 'media': 1, 'statistic': 1, 'embedding': 0, 'feature': 0, 'roc': 1, 'hyperparm': 0, 'exception': 0, 'custom': 2 }

export default { HOSTNAME, DJANGOHOSTNAME, CATEGORY, CATEGORYORDER, RUNFILESHOWFlAG, IMGURl }



